import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data(){
		return {
			isActive: 0, // day被激活
			weekList: [],
			startDate: '', // 出发日期
			startWeek: '', // 出发星期
			DepartDate: '', // 出发完整日期
			
			optDes: {}, // 接受跳转传递参数
			isShow: 0,// tabbar激活
			showItem: 1, // 显示对应的右侧页面
			screenShow: false, // 控制筛选层是否展示
			city: {
				startCity: '成都',
				endCity: '北京'
			},
			qiceTaicketList: [
				{
					startTime: '07:00',
					startStation: '赵松口站',
					endCity: '保定',
					carType: '大型',
					price: 58,
					ticketNum: 40, // 剩余票张数
				},
				{
					startTime: '07:00',
					startStation: '赵松口站',
					endCity: '保定',
					carType: '大型',
					price: 58,
					ticketNum: 40, // 剩余票张数
				},
				{
					startTime: '07:00',
					startStation: '赵松口站',
					endCity: '保定',
					carType: '大型',
					price: 58,
					ticketNum: 40, // 剩余票张数
				}
			]
			
		}
	},
	onLoad(opt){
		console.log(opt)
		this.optDes = opt
		this.setTopTimes()
		this.init()
	},
	onUnload(){
		uni.hideLoading()
	},
	components:{
		Topbar
	},
	methods: {
		init(){
			// 获取始发城市
			this.city = this.$store.state.qicheMes
			
		},
		// 设置顶部日期滚动条选择不同时间
		setTopTimes(){
			// 设置跨月依然游刃有余 skr
			let time = this.optDes.DepartDate
			console.log('初始化日期time', time)
			
			// let time = '2020-3-30'
			let timeSeconds = new Date(time).getTime()
			let oneDate = {
			}
			let box = []
			// return
			// i 小于到数值就是展示多少条日期的数值
			for (let i = 0;i<5;i++) {
				let year = new Date(timeSeconds).getFullYear() // 获取到年
				let month = new Date(timeSeconds).getMonth() + 1 // 获取到月
				let day = new Date(timeSeconds).getDate() // 获取到日期
				let showDay = month + '/' + day
				let week = new Date(timeSeconds).getDay() // 获取到周末
				 oneDate = {
					day:showDay,
					week:week
				}
				box.push(oneDate)
				timeSeconds = timeSeconds + 86400000
			}
			console.log(box)
			this.weekList = box
		},
		// day被激活
		changeIndex(item,index){
			this.isActive = index
			this.startDate = item.date
			this.startWeek = item.weekDay
			let year = new Date().getFullYear() + '/' + item.day
			
			this.DepartDate = year
			this.$store.commit('setQicheTime', year) // 重新选择出发日期 更新vuex数据
			
			
			console.log(item)
			console.log('当前完整日期', this.DepartDate)
		},
		// 切换tabbar激活
		changeShowIndex(index){
			this.isShow = index
			if(index == 1){
				this.screenShow = true
			}
		},
		// 跳转预定页面
		goTocartic(item){
			console.log('item', item)
			uni.navigateTo({
				url: '../getcartic/getcartic'
			})
		},
		// 返回上级目录
		goBack(){
			uni.navigateBack({
				
			})
		}
	}
}
